import common from '@/libs/common.mixin'

export default{
	mixins: [ common ],
	data () {
		return {
			data_uri: 'homework_publishs?with=student',
			data: []
		}
	},
	methods: {
		openNewWeb (item) {
			if(item.link_url){
				window.open(item.link_url)					
			}
		},
		detail (item) {
			this.$router.push({path:'./publish/'+item.hc_id+'?hp_id='+item.hp_id})
		}
	},
	computed: {
		style () {
			return {
				top: `${this.headerHeight}px`,
				height: `calc(100% - ${this.headerHeight}px)`
			}
		}
	}
	
}




